import { Axios } from 'axios'
import { ApiEntity } from '../core'
import { WalletBlock, WalletBlocksSearchQuery } from '../wallet-blocks'
import {
  WalletLatestBlockRoutes,
  WalletLatestBlocksClientModel,
} from './wallet-latest-block.model'

export class WalletLatestBlocksClient
  extends ApiEntity
  implements WalletLatestBlocksClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search wallet latest blocks by params
   * @param searchParams - the wallet block search params
   * @returns the promise for search wallet wallet blocks
   */
  public search(searchParams?: WalletBlocksSearchQuery) {
    return this._search<WalletBlock>(
      WalletLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * Search all wallet blocks by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: WalletBlocksSearchQuery) {
    return this._searchAll<WalletBlock>(
      WalletLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List wallet blocks by params
   * @param searchParams - the search vault blocks search params
   * @returns the promise for list blocks
   */
  public list(searchParams?: WalletBlocksSearchQuery) {
    return this._list<WalletBlock>(
      WalletLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List all epochs by params
   * @param searchParams - the search vault epochs search params
   * @returns the promise for list epochs
   */
  public listAll(searchParams?: WalletBlocksSearchQuery) {
    return this._listAll<WalletBlock>(
      WalletLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * Find an epoch by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public findOne(searchParams?: WalletBlocksSearchQuery) {
    return this._findOne<WalletBlock>(
      WalletLatestBlockRoutes.v1Search,
      searchParams
    )
  }
}
